
  import { defineComponent } from 'vue';

  export default defineComponent({
    name: 'PatternItem',
    props: {
      pattern: {
        type: Object,
      },
      index: {
        type: Number,
      },
      mode: {
        type: Number,
      },
      isActive: {
        type: Boolean,
      },
    },
    methods: {
      change: function (index: number): void {
        this.$emit('change', index as number);
      },
    },
  });
