
  import { defineComponent } from 'vue';
  import PatternItem from '@/components/PatternItem.vue';

  export default defineComponent({
    name: 'PatternList',
    props: {
      patterns: {
        type: Array,
      },
      mode: {
        type: Number,
      },
      isActive: {
        type: Boolean,
      },
    },
    components: {
      PatternItem: PatternItem,
    },
    methods: {
      change: function (index: number): void {
        this.$emit('change', index as number);
      },
    },
  });
